@import "_import";
html,body {
  &.auto {
    overflow: auto;
  }
  overflow: hidden;
  
}
.p-md {
  transition: all .3s ease-in;
  background: #fff;
  color: #000;
  @include mobile {
    overflow: auto;;
  }
  &.black {
    background: #000;
    color: #fff;
    transition: all .3s ease-in;
    .md__sec-box:before {
      background-color: #000;
      transition: background-color .3s ease-in;
    }
  }
}
.scroll-lock {
  overflow: hidden !important;
}
.l-frame__content.md {
  @include mobile {
    overflow: auto;
  -webkit-overflow-scrolling: auto;  
  }
  @at-root .br-ie & {
    overflow: hidden;
  }
}
.md__sec-box {
  @at-root .br-ie & {
    // overflow-x: hidden;
  }
  height: 100%;
  &:not(.md__sec-bg) {
    position: relative;
    &:before {
      content:'';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      z-index: -1;
      transition: background-color .3s ease-in;
    }
  }
  &3{
    
  }
  &4{
    .sec-better-symbol {
      // position: absolute;
      position: relative;
      right: 0;
      top: 100vh;
      // width: 44.37vw;
      // height: 30.88vw;
      width: 25vw;
      height: 21vw;
      background: url('/images/bg_cu_better.png') no-repeat 0 0;
      background-size: 100%;
      transform: translateY(-77%);
      z-index: -1;
      @at-root .br-ie &{
        z-index: 1;
      }
      @include mobile{
        display: none;
      }
    }
    .sec-better-symbol-mo {
      display: none;
      @include mobile{
        display: block;
        position: absolute;
        right: auto;
        left: 0;
        top: 88%;
        width: 45vw;
        height: 38vw;
        background: url('/images/bg_cu_better_mo.png') no-repeat 0 0;
        background-size: 100%;
        transform: translateY(-100);
      }
    }
  }
}

#container {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.panel{
  position: absolute;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 1.5em;
  text-align: center;
  color: white;
  box-sizing: border-box;
  padding: 10px;
  &.description {
   position: relative;
  }
}

