.visual {
  &-full-inner{
    position: relative;
    width: 100vw;
    height: 100vh;
  }
  &-center-inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 31.5vw;
    height: 34.6vw;
  }
  &__key-txt {
    // position: absolute;
    padding-top: 48rem;
    font-size: 28rem;
    line-height: 1.5;
    text-align: left;
    color: #fff;
    text-align: center;;
    font-family: 'Noto Sans KR Medium';
    @include full {
      font-size: 28px;
    }
    @include mobile{
      padding-top: 22rem;
      font-size: 18rem;
    }
    @include tablet {
      padding-top: 35rem;
      font-size: 23rem;
    }
    & > span {
      display: block;;
    }
    &-box {
      @include text-effect('.txt-target');
      &.stack-target {
        &.stack--on {
          opacity: 1;
          transition: 0.3s ease all;
        }
      }
    }
    

  }
  .txt-unit {
    &-wrap {
      z-index: -1;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      text-align: center;;
      // width: 100%;
      @include mobile {
        white-space: nowrap;
      }
      // height: 100%;
    }
    &__tit {
      @include text-effect('.txt-target');
      // position: absolute;
