    line-height: 1;
    vertical-align: middle;
    background: #000;
    border-radius: 80px;
    transform: translateX(-50%);
    z-index: 9;
    overflow: hidden;
    transition: opacity 0.3s .8s, width 0.8s 0s;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.15);
