          padding: 3px 0 0;
        }
      }
    }
    @at-root .br-ie &{
      margin-top : 8px;
      &-btn {
        &--active {
          margin-top: -3px;
        }
      }
    }
    @at-root .br-win-ch & {
      &-btn:not(#{$self}__item-btn--active) {
        padding: 4px 0 0;
      }
    }
    @include full {
      font-size: 20px;
    }
    @include mobile{
      margin: 0 8rem;
      float: left;
      font-size: 10px;
      font-weight: bold;
      line-height: 1;
      &-btn {
        letter-spacing: 0.6px;
        margin-top:-4px;
        &--active {
          font-size: 10px;
        }
      }
    }
  }
  &--hide{
    opacity: 0 !important;
  }
  &--active{
    #{$self}__nav{
      opacity: 0;
      width: 100% !important;
      transition: opacity 0.3s, width 2s;
      @include mobile{
        transition: opacity 0.3s, width 1s;
      }
    }
    #{$self}__list{
      z-index: 10;
      width: 100% !important;
      opacity: 1;
      transition: opacity 0.3s, width 2s;
      @include mobile{
        transition: opacity 0.3s, width 1s;
      }
    }
    #{$self}__item{
      opacity: 1;
      pointer-events: auto;
      transition: opacity 0.5s 0.5s;
      @include mobile{
        transition: opacity 0.5s;
      }
    }
  }
}

// geommyeong moeified-- start
@mixin idontcarescroll {
  visibility: visible;
  .txt-target {
    display: block;
    transform: translate(0,0) rotate(.001deg);
    -ms-transition: transform 1s cubic-bezier(.15,.85,.45,1),opacity 1s cubic-bezier(.32,.94,.6,1);
    transition: transform 1s cubic-bezier(.15,.85,.45,1),opacity 1s cubic-bezier(.32,.94,.6,1);

    @for $i from 1 to 8 {
      &:nth-child(#{$i}) {
        transition-delay: #{$i * 0.1s};
      }
    }
  }
  .pic-card__txt-sub {
    .txt-target {
      @for $i from 1 to 8 {
        &:nth-child(#{$i}) {
          transition-delay: #{($i * 0.1s) + 0.3s};
        }
      }
    }
  }
}


.pic-card__txt.pic-card__target {
  padding: 3rem 0 0;
  @at-root .br-ie & {
    padding: 17rem 0 0;
  }
  @include text-effect('.txt-target');
  .txt-target {
    opacity: 0;
    display: block;
  }
  &.hello {
    .txt-target {
      opacity: 1;
    }
    @include idontcarescroll;
  }
}


.visual {
  .stack-target {
    @include text-effect('.txt-target');
    .txt-target {
      opacity: 0;
      display: block;
    }
    &.stack--on{
      @include idontcarescroll;
      overflow: hidden;
      .txt-target {
        opacity: 1;
      }
    }
  }
}

@include mobile {
  .md__sec-picnic,
  .md__sec-better {
    .pic-card__txt.pic-card__target.hello {
      .pic-card__txt-tit {
        .txt-target.mobile-inline {
          display: inline-block;
          &:nth-child(2) {
            transition-delay: 0.1s;
          }
        }
      }
    }
    .pic-card__txt.pic-card__target {
      .pic-card__txt-tit {
        .txt-target.mobile-inline {
          display: inline-block;
        }
      }
    }
  }
}


